import {
	mapState,
	mapMutations
} from 'vuex'

export default {
	onLoad() {},
	data() {
		return {
			phoneno: 'ouke123',
			password: 'ek2015'
		};
	},
	methods: {
		...mapMutations(['login','setDicts']),
		bindLogin() {
			if (this.phoneno.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '账号最短为 6 个字符'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			this.$api.login([this.phoneno, this.password]).then((res) => {
				//用户信息存储在store中
				res.data.phoneno = this.phoneno
				res.data.loginInfo = this.password
				this.login(res.data)
				return this.$api.getAllDicts(null)
			}).then(res => {
				let dic = {};
				res.data.forEach(obj => {
					if (dic[obj.dictCode]) {
						dic[obj.dictCode].push(obj);
					} else {
						dic[obj.dictCode] = [];
						dic[obj.dictCode].push(obj);
					}
				})
				this.setDicts(dic)
				this.toMain()
			})
		},
		toMain(userInfo) {
			/**
			 * 强制登录时使用reLaunch方式跳转过来
			 * 返回首页也使用reLaunch方式
			 */
			uni.reLaunch({
				url: '../index/index',
			});
		}
	}
}
